body {
  font-family: "Montserrat Variable", "Montserrat", sans-serif;
  margin: 0;
}

button {
  font-family: "Montserrat Variable", "Montserrat";
  cursor: pointer;
  outline: none;
}

a {
  text-decoration: none;
  color: inherit;
}

a:active {
  color: inherit;
}


.custom-radio, .custom-checkboxes {
  display: flex;
  align-items: center;
  cursor: pointer;
  user-select: none;
}

.custom-radio>input, .custom-checkboxes>input {
  position: absolute;
  z-index: -1;
  opacity: 0;
  width: 0 !important;
}

.custom-radio>.custom-radio-span::before, .custom-checkboxes>.custom-checkboxes-span::before {
  content: '';
  display: inline-block;
  width: 30px;
  height: 30px;
  flex-shrink: 0;
  flex-grow: 0;
  border-radius: 2px;
  border: 1px solid var(--shapes-grey-1-transparency, rgba(192, 192, 192, 0.60));
  margin-right: 10px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 94% 131%;
  margin-top: 4px;
}

.custom-radio>.custom-radio-span::before {
  border-radius: 100%;
}

