.errorMessage {
  color: #DF0003;
  font-size: 12px;
  font-weight: 400;
  position: absolute;
  bottom: -25px;
}

.errorMessage2 {
  color: #DF0003;
  font-size: 16px;
  font-weight: 400;
  position: absolute;
  bottom: -40px;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.overlay {
  z-index: 19;
  position: fixed;
  background: rgba(0,0,0,.8);
  width: 100%;
  height: 101%;
  overflow: hidden;
  top: 0;
  left: 0;
  cursor: pointer;
}

input[type=submit]:disabled {
  opacity: 0.5;
  background: #B4B4B4;
  pointer-events: none;
}

@media screen and (max-width: 1220px) {
  .container {
    padding: 0 10px;
  }
}